import React from 'react';
import { BrowserRouter as Router, Routes, Route } from 'react-router-dom';
import { Container } from '@mui/material';
import Navbar from './components/layout/Navbar';
import LoginForm from './components/auth/LoginForm';
import RegisterForm from './components/auth/RegisterForm';
import PizzaList from './components/pizza/PizzaList';
import PizzaForm from './components/pizza/PizzaForm';
import ApiTest from './components/api/ApiTest';

function App() {
  return (
    <Router>
      <Navbar />
      <Container maxWidth="lg" sx={{ mt: 4 }}>
        <Routes>
          <Route path="/" element={<PizzaList />} />
          <Route path="/login" element={<LoginForm />} />
          <Route path="/register" element={<RegisterForm />} />
          <Route path="/pizzas" element={<PizzaList />} />
          <Route path="/pizzas/new" element={<PizzaForm />} />
          <Route path="/pizzas/:id/edit" element={<PizzaForm />} />
          <Route path="/api/:apiType" element={<ApiTest />} />
        </Routes>
      </Container>
    </Router>
  );
}

export default App; 